.card:hover {
  -webkit-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  -moz-box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  box-shadow: 0px 7px 10px 2px rgba(34, 60, 80, 0.2);
}
.card {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-direction: column;
}
